@media screen and (min-width: 700px) {
  .s-h {
    text-align: center;
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 18px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }

  .s-para {
    text-align: center;
    line-height: 30px;
  }

  .item {
    box-shadow: 1px 2px 14px gainsboro;
    width: 300px;
    height: 300px;
    border-radius: 8px 8px 0px 0px;
    overflow: hidden;
  }
  .nav {
    width: 100%;
    height: 26px;
    background-color: gray;
    padding: 2px;
    border-radius: 8px 8px 0px 0px;
    position: sticky;
    z-index: 999;
  }

  span {
    background-color: white;
    margin: 2px 8px;
    padding: 1px 8px;
    border-radius: 25px;
  }

  .web-box {
    margin-top: 18px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    height: 115vh;
    align-items: center;
    padding: 0px 170px;
  }

  img.site {
    object-fit: cover;
    width: 100%;
    transition: 0.8s ease-in-out;
    cursor: pointer;
  }

  img.site:hover {
    transform: scale(1.2);
  }
}

/*#######--mobile--############*/

@media screen and (max-width: 599px) {
  .s-h {
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 18px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }

  .s-para {
    text-align: center;
    line-height: 20px;
  }

  .item {
    box-shadow: 1px 2px 14px gainsboro;
    width: 300px;
    height: 300px;
    border-radius: 8px 8px 0px 0px;
    overflow: hidden;
  }
  .nav {
    width: 100%;
    height: 26px;
    background-color: gray;
    padding: 2px;
    border-radius: 8px 8px 0px 0px;
    position: sticky;
    z-index: 999;
  }

  span {
    background-color: white;
    margin: 2px 8px;
    padding: 1px 8px;
    border-radius: 25px;
  }

  .web-box {
    margin-top: 18px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    height: 115vh;
    align-items: center;
    padding: 0px 0px;
  }

  img.site {
    object-fit: cover;
    width: 100%;
    animation: scal 4s linear infinite;
    cursor: pointer;
  }

  @keyframes scal {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(1.2);
    }
  }
  .item {
    margin-top: 40px;
  }

  .lurix {
    margin-top: 270px;
  }
}
